body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app-header {
    color: white;
    background-color: #f5f8fa;
    padding: 13vh 20px 0;
    border: 1px solid rgba(0,0,0,.125);
    text-align: center;
    height: 100vh;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: #1F2834;
}

.app-title {
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 50px;
}

.app-sub-title {
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 30px;
}

.down, .up{
    font-size: 100px;
    margin: 0 auto;
    -ms-flex: 2 1;
        flex: 2 1;
    color: #00FFF4;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    background: transparent;
    outline:none;
    border: none;
    max-height: 230px;
    width: 114px;
}

.down:hover, .up:hover {
    color: #23527c;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.up {
    color: #00FFF4;
    font-size: 100px;
    margin: 20px;
}

.container {
    padding: 0;
    margin: 0 auto;
    width: 100%;
}



.promo-image-modal {
    max-width: 100%;
    display: block;
    margin: 0 auto;
}

.section-dark .section-heading {
    text-align: center;
    color: white;
}


.section-light .section-heading {
    color: black;
    text-align: center;
}

.article-heading {
    color: #00FFF4;
}

.tags {
    color: #23527c;
    font-size: 12px;
}

.tags span {
    color: #C5C6C8;
}

.section-dark .article-content {
    color: #C5C6C8;
}

.section-light{
    min-height: 100vh;
    padding-bottom: 50px;
    display: inline-block;
    width: 100%;
    background-color: #f5f8fa;
    color: black;
}

.section-dark {
    padding-bottom: 50px;
    display: inline-block;
    width: 100%;
    background-color: #1F2834;
    position: relative;
}

.section-dark article {
    color: white;
}

hr {
    display: block;
    height: 1px;
    border: 0;

    margin: 6em 0;
    padding: 0;
}

.footer-social {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.footer-social svg{
    font-size: 20px;
    margin: 0 16px;
}

footer a, footer a:visited {
    color: black;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

footer a:hover {
 color: #1F2834;
 -webkit-animation: 0.7s multicolor;
         animation: 0.7s multicolor;
}

@-webkit-keyframes multicolor {
    0% {
        color: black;
    }
    30% {
        color: #00FFF4;
    }
    80% {
        color: #1F2834;
    }
}

@keyframes multicolor {
    0% {
        color: black;
    }
    30% {
        color: #00FFF4;
    }
    80% {
        color: #1F2834;
    }
}

footer {
    display: inline-block;
    width: 100%;
    padding: 49px 22px;
}

.language {
    position: fixed;
    top: 20px;
    right: 20px;
    cursor: pointer;
    z-index: 999;
}


.full-height {
    height: 100%;
    float: left;
}

.full-width {
    width: 100%;
    float: left;
}

.center {
    margin: 0 auto;
}

.center-text {
    text-align: center;
}

p {
    font-size: 2rem;
}

#avatar {
    background-image: url('./images/avatar.jpg');
    width: 150px;
    height: 150px;
    background-size: cover;
    background-position: top center;
    border-radius: 50%;
    margin: 22px auto;
    min-height: 150px;
}

#what-i-do {
    padding: 13vh 20px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

#portfolio h4 {
    color: #C5C6C8;
}

.flex {
    -ms-flex: 1 1;
        flex: 1 1;
}

#portfolio nav button {
    float: right;
    background: transparent;
    outline: none;
    color: #ACADAE;
    border: none;
    Font-Family: 'Montserrat', sans-serif;
    font-size: 15px;
}

#portfolio nav button.active {
    color: white;
}

#portfolio nav {
    float: left;
    -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotate(-90deg);
    margin-top: 292px;
    margin-left: -300px;
    position: absolute;
    width: 500px;
}


.width {
   max-width: 640px;
}

.open-item {
    padding: 0;
}

.open-item img {
    border-radius: 6px;
}

.modal-body {
    background: transparent;
    padding: 0;
}

.modal-backdrop {
    opacity: 0.8;
}

.modal-content {
    background: #1F2834;
}

.modal-dialog {
    max-width: 1000px;
    width: 800px;
    padding: 20px;
}


.up-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}


button {background: transparent;
    outline: none;
    border: none;
}

.home {
    position: fixed;
    top: 20px;
    left: 20px;
    color: #00FFF4;
    padding: 0;
    z-index: 2;
}
.home h4 {
    padding: 0;
    margin: 0;
}



/* fonts */

h1 {
    Font-Family: 'Montserrat', sans-serif;
  Font-Size: 48px;
}

h2 {
    Font-Family: 'Montserrat', sans-serif;
    Font-Size: 32px;
}

h3 {
    Font-Family: 'Montserrat', sans-serif;
    Font-Size: 20px;
}

h4 {
    Font-Family: 'Montserrat', sans-serif;
}

p {
 Font-Family: 'Montserrat', Serif;
 Font-Size: 18px;
 line-height: 28px;
}



